    };

    private resultDate: Model.IScheduleDate[] = [];

    private eFormMode = EFormMode;
    private loading = new Loading();
    //#endregion

    //#region Computed
    private get formAddScheduleDisable(): boolean {
        return !this.formData.week || !this.formData.startTime || !this.formData.endTime;
    }
