    //#region Watch
    //#endregion

    //#region Vue Life
    private async beforeCreate(): Promise<void> {}
    private async created(): Promise<void> {}
    private async beforeMount(): Promise<void> {
        await this.initSelectionItem();
        await this.initData(FakeData);
    }
    private async mounted(): Promise<void> {}
    private async beforeDestroy(): Promise<void> {}
    private async destroyed(): Promise<void> {}
    //#endregion

    //#region Init
    //#region Init form
    private async initData(rowData: Model.ITableData): Promise<void> {
        this.initFormConfig();
        await this.formConfigSet(rowData);
    }

    private initFormConfig(): void {
        // name
        this.formConfig.name.id = 'name';
        this.formConfig.name.label = this.$i18n.Common_Name;
        this.formConfig.name.placeholder = this.$i18n.Common_Name;
        this.formConfig.name.formType = EFormType.text;
        this.formConfig.name.required = true;
        this.formConfig.name.cols = 11;

        // schedules
        this.formConfig.schedules.id = 'schedules';
        this.formConfig.schedules.formType = EFormType.slot;

        this.formConfigs = [this.formConfig.name, this.formConfig.schedules];
    }

    private initFormData(): void {
        this.initFormConfig();
    }

    private initFormAddSchedules(): void {
        this.formItem.week = {
            objectId: Model.EWeekday.monday,
            name: this.$i18n.Date_Monday,
        };
        this.formItem.startTime = new Date('2000/01/02 00:00');
        this.formItem.endTime = new Date('2000/01/02 23:59');
        this.formItem.isAllDay = false;
    }
    //#endregion

    //#region Init select option
    private async initSelectionItem(): Promise<void> {
        this.selectOptionItem.weeks = [
            { objectId: Model.EWeekday.sunday, name: this.$i18n.Date_Sunday },
            { objectId: Model.EWeekday.monday, name: this.$i18n.Date_Monday },
            { objectId: Model.EWeekday.tuesday, name: this.$i18n.Date_Tuesday },
            { objectId: Model.EWeekday.wednesday, name: this.$i18n.Date_Wednesday },
            { objectId: Model.EWeekday.thursday, name: this.$i18n.Date_Thursday },
            { objectId: Model.EWeekday.friday, name: this.$i18n.Date_Friday },
            { objectId: Model.EWeekday.saturday, name: this.$i18n.Date_Saturday },
        ];
    }
    //#endregion
    //#endregion

    //#region View Event
    //#region Event page

    //#endregion

    //#region Event table
    //#endregion

    //#region Event form
    private formConfigValues(value: any, disabledButton: boolean, id: string): void {
        if (this.formData.hasOwnProperty(id)) this.formData[id] = value;
        if (this.formButtonDisabled.hasOwnProperty(id)) this.formButtonDisabled[id] = disabledButton;
    }

    private fromUpdateIsAllDay(): void {
        if (this.formItem.isAllDay) {
            this.formItem.startTime = new Date('2000/01/02 00:00');
            this.formItem.endTime = new Date('2000/01/02 23:59');
        }
    }
    //#endregion

    //#region Event add schedules
    private formAddSchedules(): boolean {
        let _startTime: Date = ServiceDatetime.normalizeTime(this.formItem.startTime);
        let _endTime: Date = ServiceDatetime.normalizeTime(this.formItem.endTime);
        let isEndTime0000: boolean = ServiceDatetime.toString(_endTime, EDatetimeFormat.timeHHmm) !== '00:00';

        if (_startTime.getTime() >= _endTime.getTime() && isEndTime0000) {
            ServiceDialog.ErrorConfirmOnly(this, this.$i18n.Schedule_ErrorTime);
            return false;
        } else {
            _endTime = ServiceDatetime.alignDate(_endTime, ServiceDatetimeClass.EAlignType.day, 1);
        }

        const tempWeeks: Model.ISelectionWeek[] = [];
        const tempStartTimes: Date[] = [];
        const tempEndTimes: Date[] = [];
        const tempIsAllDays: boolean[] = [];

        const isOverlap: ServiceDatetimeClass.IRangeOverlapAllRepsonse = this.resolveSchedulesIsOverloop(
            tempWeeks,
            tempStartTimes,
            tempEndTimes,
            tempIsAllDays,
        );

        if (this.formData.week.length > 0) {
            const tempWeeks: Model.ISelectionWeek[] = (this.formData.week || []).map((x) => x);
            const tempStartTimes: Date[] = (this.formData.startTime || []).map((x) => x);
            const tempEndTimes: Date[] = (this.formData.endTime || []).map((x) => x);
            const tempIsAllDays: boolean[] = (this.formData.isAllDay || []).map((x) => x);

            tempWeeks.push(this.formItem.week);
            tempStartTimes.push(this.formItem.startTime);
            tempEndTimes.push(this.formItem.endTime);
            tempIsAllDays.push(this.formItem.isAllDay);

            const isOverlap: ServiceDatetimeClass.IRangeOverlapAllRepsonse = this.resolveSchedulesIsOverloop(
                tempWeeks,
                tempStartTimes,
                tempEndTimes,
                tempIsAllDays,
            );

            let overlapError: string = `${this.$i18n.Schedule_Schedule_Overlap} <br>`;

            if (isOverlap.isOverlap) {
                (isOverlap.results || []).forEach((item) => {
                    if (item.isOverlap) {
                        overlapError += `${this.$i18n.Schedule_No}${item.now + 1}${this.$i18n.Schedule_AndNo}${item.overlap + 1}${
                            this.$i18n.Schedule_Schedule_Overlap
                        } <br>`;
                    }
                });
                let Title: string = this.$i18n.Status_Error;
                ServiceDialog.ErrorConfirmOnly(this, overlapError, Title);
            } else {
                this.formData.week.push(this.formItem.week);
                this.formData.startTime.push(this.formItem.startTime);
                this.formData.endTime.push(this.formItem.endTime);
                this.formData.isAllDay.push(this.formItem.isAllDay);

                this.initFormAddSchedules();
            }
        } else {
            this.formData.week.push(this.formItem.week);
            this.formData.startTime.push(this.formItem.startTime);
            this.formData.endTime.push(this.formItem.endTime);
            this.formData.isAllDay.push(this.formItem.isAllDay);

            this.initFormAddSchedules();
        }
    }

    private formRemoveSchedules(index: number): void {
        this.formData.week.splice(index, 1);
        this.formData.startTime.splice(index, 1);
        this.formData.endTime.splice(index, 1);
        this.formData.isAllDay.splice(index, 1);
    }
    //#endregion

    //#region Event server api (save, delete, test, sync...)
    //#endregion
    //#endregion

    //#region Other Function
    //#region Table
    //#endregion

    //#region Form
    private formTitle(title: string = ''): string {
        return title;
    }

    private formDataClear(): void {
        this.formData = JSON.parse(JSON.stringify({ ...this.formDataOriginal }));
        this.formData.week = [];
        this.formData.startTime = [];
        this.formData.endTime = [];
        this.formData.isAllDay = [];

        this.formItem.isClearState = false;

        this.initFormAddSchedules();
    }

    private async formConfigSet(data: Model.ITableData = undefined): Promise<void> {
        if (data) await this.formDataSet(data);
    }

    private async formDataSet(data: Model.ITableData): Promise<void> {
        this.formData.objectId = data.objectId ?? '';
        this.formData.name = data.name ?? '';
        this.resolveWeek(data.ranges);
        this.resolveTime(data.ranges);

        this.resolveSchedulesIsOverloop(this.formData.week, this.formData.startTime, this.formData.endTime, this.formData.isAllDay);

        this.formItem.isClearState = false;
    }
    //#endregion

    //#region Server response
    //#endregion

    //#region Resolve text
    private resolveSchedulesIsOverloop(
        weeks: Model.ISelectionWeek[],
        startTimes: Date[],
        endTimes: Date[],
        isAllDays: boolean[],
    ): ServiceDatetimeClass.IRangeOverlapAllRepsonse {
        let resultDate: Model.IScheduleDate[] = [];
        let tempResult = null;

        // 
        let tempAllDate = null;
        let tempStartDate = null;
        let tempEndDate = null;

        for (let i in weeks) {
            let tempWeek = weeks[i].objectId;
            let tempStartTime = startTimes[i];
            let tempEndTime = endTimes[i];
            let isEndTime0000: boolean = ServiceDatetime.toString(tempEndTime, EDatetimeFormat.timeHHmm) === '00:00';

            if (isAllDays[i]) {
                tempAllDate = ServiceDatetime.normalizeDayAndTime(tempWeek, tempStartTime, tempEndTime);
                tempStartDate = tempAllDate.startDate;
                tempEndDate = ServiceDatetime.dateStart(ServiceDatetime.dateByStep(tempAllDate.startDate, 1));
                tempResult = {
                    startDate: tempStartDate,
                    endDate: tempEndDate,
                };
            } else if (isEndTime0000) {
                tempStartDate = ServiceDatetime.normalizeDayAndTime(tempWeek, tempStartTime, tempStartTime).startDate;
                tempEndDate = ServiceDatetime.dateStart(ServiceDatetime.dateByStep(tempStartDate, 1));
                tempResult = {
                    startDate: tempStartDate,
                    endDate: tempEndDate,
                };
            } else {
                tempResult = ServiceDatetime.normalizeDayAndTime(tempWeek, tempStartTime, tempEndTime);
            }

            resultDate.push(tempResult);

            this.resultDate = resultDate;
        }

        return ServiceDatetime.checkRangesOverlapAll(resultDate);
    }

    private resolveWeek(schedules: Model.IScheduleRange[]): void {
        this.formData.week = [];
        (schedules || []).forEach((schedule, scheduleIndex) => {
            let tempStartDate: string = ServiceDatetime.datetime2String(new Date(schedule.startDate), 'D');
            let tempEndDate: string = ServiceDatetime.datetime2String(new Date(schedule.endDate), 'D');
            let isStartTime0000: boolean = ServiceDatetime.toString(new Date(schedule.startDate), EDatetimeFormat.timeHHmm) === '00:00';
            let isEndTime0000: boolean = ServiceDatetime.toString(new Date(schedule.endDate), EDatetimeFormat.timeHHmm) === '00:00';

            if (parseInt(tempEndDate, 10) - parseInt(tempStartDate, 10) === 0) {
                this.formData.isAllDay.push(false);
            } else if (parseInt(tempEndDate, 10) - parseInt(tempStartDate, 10) === 1) {
                if (isEndTime0000) {
                    if (isStartTime0000) {
                        this.formData.isAllDay.push(true);
                    } else {
                        this.formData.isAllDay.push(false);
                    }
                } else {
                    this.formData.isAllDay.push(true);
                }
            }

            this.selectOptionItem.weeks.forEach((week) => {
                if (new Date(schedule.startDate).getDay() === week.objectId) {
                    this.formData.week.push(week);
                }
            });
        });
    }

    private resolveTime(schedules: Model.IScheduleRange[]): void {
        this.formData.startTime = [];
        this.formData.endTime = [];

        (schedules || []).forEach((schedule) => {
            this.formData.startTime.push(new Date(schedule.startDate));
            this.formData.endTime.push(new Date(schedule.endDate));
        });
    }

    private resolveDate(date: string | Date): string {
        return ServiceResolveText.resolveDate(date, this.formItem.timeFormat);
    }
    //#endregion
    //#endregion
}
